::-webkit-scrollbar-thumb {
    background: #39A2DB; 
    border-radius: 2px;
  }
  ::-webkit-scrollbar {
    width: 20px;
    background-color: #212529;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 2px grey; 
    border-radius: 2px;
  }
   
.navbar1 {
  background-color: #212529;
  height: 100px;
  padding: 50px;
  position: fixed;
  top: 0;
  
  transition: 0.5s;
  width: 100%;
}
.navbar2 {
  background-color: #212529;
  z-index: 1;
  height: 100px;
  padding-right: 40px;
  position: fixed;
  width: 100%;
  transition: 0.5s;
  box-shadow: 10px 5px 20px #212529;
}
.navbar-collapse{
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
.navbar .navbar-brand {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  color: #39A2DB;
  font-size: 2em;
  margin-left: 40px;

}
.navbar .navbar-nav .nav-link {
  color: #DEEEEA;
  margin-left: 50px;
  font-family: 'Oswald', sans-serif;
  word-spacing: 3px;
}
.navbar .navbar-brand:focus{
  color: #39A2DB;
}


.navbar-light .navbar-nav .nav-link.active, .navbar-light .navbar-nav .show>.nav-link{
  color: #39A2DB;
}
.navbar .navbar-brand:hover,

.navbar .navbar-nav .nav-link:hover,
.navbar .navbar-nav .nav-link:focus {
  color: #39A2DB;
}

@media only screen and (max-width: 320px) {
  .navbar1 {
    padding: 20px 0 0 0;
  }
  .ml-auto {
    background-color: #212529;
    background-color: visible;
  }
  .navbar2 {
    padding: 20px 0 0 0;
  }
  .navbar .navbar-nav {
    -webkit-flex-direction: row;
            flex-direction: row;
  }
  .navbar .navbar-nav .nav-link {
    margin: auto;
  }
  .navbar .navbar-brand{
    margin-left: 20px;
  }
}
